import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector, connect } from 'react-redux';
import { Snackbar } from '@material-ui/core';
import { useHistory } from 'react-router-dom';

import { positionsActions, devicesActions, sessionActions } from './store';
import { useEffectAsync } from './reactHelper';
import { useTranslation } from './LocalizationProvider';
import { prefixString } from './common/stringUtils';

const SocketController = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const t = useTranslation();

  const authenticated = useSelector((state) => !!state.session.user);
  const devices = useSelector((state) => state.devices.items);

  const socketRef = useRef();

  const [events, setEvents] = useState([]);
  const [notifications, setNotifications] = useState([]);

  const connectSocket = () => {
    const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
    //const socket = new WebSocket(`${protocol}//${window.location.host}/api/socket`);
    //const socket = new WebSocket(`${protocol}//${process.env.REACT_APP_URL_NAME}/api/socket`);
	//const socket = new WebSocket(`${protocol}//`.`${process.env.REACT_APP_URL_NAME}/api/socket`);
	//const socket = new WebSocket('ws://139.59.81.79:8082/api/socket', {ws: true});
	//const socket = new WebSocket('wss://139.59.81.79:8082/api/socket', "json");
	const socket = new WebSocket('wss://base.trakware.org/api/socket');
	socketRef.current = socket;

    socket.onerror = () => {
      setTimeout(() => connectSocket(), 60 * 1000);
    };

    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);
      if (data.devices) {
        dispatch(devicesActions.update(data.devices));
      }
      if (data.positions) {
        dispatch(positionsActions.update(data.positions));
      }
      if (data.events) {
        setEvents(data.events);
      }
    };
  };

  useEffectAsync(async () => {
    //const response = await fetch('/api/server');
	//const response = await fetch('https://${process.env.REACT_APP_URL_NAME}/api/server');
    //const response = await fetch('https://139.59.81.79:8082/api/server', { mode: 'no-cors'});
    //const response = await fetch('https://139.59.81.79:8082/api/server');
	//const response = await fetch('https://139.59.81.79:8082/api/server');
    //const response = await fetch('https://${process.env.REACT_APP_URL_NAME}/api/server');
	const response = await fetch(`https://${process.env.REACT_APP_URL_NAME}/api/server`);
    //Changed process.env.REACT_APP_URL_NAME in env.local from 139.59.81.79:8082 TO https://base.trakware.org
	//Test response from Server using Browser eg enter URL and see response : https://base.trakware.org/api/server
	
	if (response.ok) {
      dispatch(sessionActions.updateServer(await response.json()));
    }
  }, []);

  useEffectAsync(async () => {
    if (authenticated) {
      const response = await fetch(`https://${process.env.REACT_APP_URL_NAME}/api/devices`);
	  if (response.ok) {
        dispatch(devicesActions.refresh(await response.json()));
      }
      connectSocket();
      return () => {
        const socket = socketRef.current;
        if (socket) {
          socket.close();
        }
      };
    }
    const response = await fetch(`https://${process.env.REACT_APP_URL_NAME}/api/session`);
	if (response.ok) {
      dispatch(sessionActions.updateUser(await response.json()));
    } else {
      history.push('/login');
    }
    return null;
  }, [authenticated]);

  useEffect(() => {
    setNotifications(events.map((event) => ({
      id: event.id,
      message: `${devices[event.deviceId]?.name}: ${t(prefixString('event', event.type))}`,
      show: true,
    })));
  }, [events, devices]);

  return (
    <>
      {notifications.map((notification) => (
        <Snackbar
          key={notification.id}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          open={notification.show}
          message={notification.message}
          autoHideDuration={5000}
          onClose={() => setEvents(events.filter((e) => e.id !== notification.id))}
        />
      ))}
    </>
  );
};

export default connect()(SocketController);
